#addSubscriptionsContainer #subscriptionContainer {
  background: #fdfdfd;
  border-radius: 3px;
  box-shadow: 5px 5px 5px #dfe1e2;
  border: 1px solid #dfe1e2;
}
#addSubscriptionsContainer #addPostForm {
  width: 100% !important;
  max-width: 30rem;
}

#viewSubscriptionsContainer {
  background: linear-gradient(#f0f0f0, #ffffff, #ffffff);
}
#viewSubscriptionsContainer #tableContainer {
